$cr_1: #ff2f96;

.examples {
	width: 100%;
	height: 854px;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	@media (max-width: 1366px) {
		height: 1050px; }
	@media (max-width: 1024px) {
		height: 1047px; }
	@media (max-width: 800px) {
		height: 700px; }
	@media (max-width: 320px) {
		height: 612px; }
	& .headliner {
		padding-top: 84px;
		margin-bottom: 95px;
		@media (max-width: 1024px) {
			padding-top: 90px;
			margin-bottom: 95px; }
		@media (max-width: 800px) {
			width: 218px;
			font-size: 30px;
			text-align: center;
			padding-top: 50px;
			margin-bottom: 50px; }
		@media (max-width: 320px) {
			padding-top: 0;
			margin-bottom: 40px; } }
	& .fixed-container {
		@media (max-width: 997px) {
			display: flex;
			justify-content: center; } }
	& .album {
		height: 350px;
		display: flex;
		position: relative;
		width: 97.1%;
		margin: 0 auto;
		@media (max-width: 1366px) {
			width: 370*2+30px;
			justify-content: center; }
		@media (max-width: 1024px) {
			height: 408px;
			width: 948px; }
		@media (max-width: 997px) {
			width: 456px; }
		@media (max-width: 800px) {
			display: block;
			position: static;
			width: 320px;
			height: 362px; }
		@media (max-width: 384px) {
			width: 280px; }
		& img {
			margin-bottom: 35px;
			@media (max-width: 1024px) {
				width: 456px;
				margin-bottom: 22px; }
			@media (max-width: 800px) {
				width: 320px;
				height: 250px;
				margin-bottom: 16px; }
			@media (max-width: 384px) {
				width: 280px; } }
		& .text-link {
			text-align: center; }
		& a {
			text-decoration: none;
			color: gray;
			text-align: center;
			font-size: 20px;
			@media (max-width: 1024px) {
				font-size: 25px; }
			@media (max-width: 800px) {
				font-size: 18px; }
			&:hover {
				color: $cr_1; } } }
	& .text_link {
		margin: 20px 0 0 0;
		display: flex;
		justify-content: center;
		padding: 0 20px 0 0;
		font-size: 20px; }
	& .examples__button_action {
		@media (max-width: 1024px) {
			margin: 191px 0 0 0; }
		@media (max-width: 800px) {
			margin: 0;
			width: 280px; } } }
