button {
	cursor: pointer; }

button[class$="call"] {
	width: 207px;
	height: 45px;
	color: #fc557c;
	background-color: white;
	border: 1px solid #fc557c;
	&:hover {
		animation-duration: 3s;
		transform: perspective(50px) rotateY(-2deg);
		transform-origin: 50% 0%; }

	@media (max-width: 1024px) {
		font-size: 16px; } }


button[class$="action"] {
	width: 270px;
	height: 60px;
	font-size: 18px;
	color: white;
	background-color: #ff4e2e;
	border: none;
	margin: auto 0 auto 0;
	&:hover {
		animation-duration: 3s;
		transform: perspective(50px) rotateY(2deg);
		transform-origin: 50% 50%; }
	@media (max-width: 1024px) {
		width: 320px;
		height: 68px; }
	@media (max-width: 800px) {
		height: 55px;
		font-size: 16px;
		margin: auto auto 50px auto; } }

img[class$="call"] {
	display: none;
	@media (max-width: 800px) {
		display: block; } }
